import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom';             


export default function Navbar(props) {
  return (
    <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div className="container-fluid">
    <a className="navbar-brand" href="#">{props.title}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          {/* <Link className="nav-link active" aria-current="page" to="/home">Home</Link> */}
          <a className="nav-link active" aria-current="page" to="/home">Home</a>
        </li>
        <li className="nav-item">
          {/* <Link className="nav-link" to="/about">{props.aboutText}</Link> */}
        </li>
        <li className="nav-item">
          <a className="nav-link" href='/'>Contact</a>
        </li>
      </ul>
      <form className="d-flex">
        {/* <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
        <button className="btn btn-outline-success" type="submit">Search</button> */}
        <input type="radio" class="btn-check" name="options-outlined" id="danger-outlined" autocomplete="off" onClick={props.dangerColorBtn} />
        <label class="btn btn-outline-danger" for="danger-outlined">Danger radio</label>
        <div class={`form-check form-switch text-${props.mode === 'light'?'dark':'light'}`}>
            <input className="form-check-input text" onClick={props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault" />
            <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Dark Mode</label>
        </div>
      </form>
    </div>
  </div>
</nav>
  )
}
Navbar.propTypes = {title: PropTypes.string.isRequired,
                    aboutText: PropTypes.string.isRequired
}
Navbar.defaultProps = {
    title: "Set title here",
    aboutText: "About text here"
};

