import React, {useState} from 'react'


export default function FormText(props) {
  
    const handleUpClick = () =>{
        console.log("Uppercase button was Clicked"  + text);
        let dummyText = text.toUpperCase()
        setText(dummyText)
    }
    const handleOnChange = (event) =>{
        console.log("Handle On Change was clicked");
        setText(event.target.value);
       
    }
    const handleLowClick = () =>{
        let lowText = text.toLowerCase();
        setText(lowText);
    }
    const handlereverseClick = () =>{
        let repText = text.split("").reverse().join("");
        setText(repText)
    }
    const handlerclearClick = () =>{
       let clearText = "";
       setText(clearText);
    }
   
    const [text, setText] = useState('');
    
    return (
        <>
        <div className="container">
        <div>
            <div className="mb-3">
                <label for="myBox" className="form-label">{props.heading}</label>
                <textarea className="form-control"  style={{backgroundColor:props.mode==='dark'?'grey':'white'}} id="myBox" rows="15" value={text} onChange={handleOnChange} ></textarea>
            </div>
            <button className="btn btn-primary" onClick={handleUpClick}>Convert To UpperCase</button>
            <button className="btn btn-primary mx-4" onClick={handleLowClick}>Convert To LowerCase</button>
            <button className="btn btn-primary mx-4" onClick={handlereverseClick}>Replace String</button>
            <button className="btn btn-primary mx-4" onClick={handlerclearClick}>Clear All</button>
        </div>
        </div>
        <div className="container my-4">
            <h1>Your Text Summary</h1>
            <p>{text.split("").length} Words and {text.length} Characters</p>
            <p>{0.008 * text.split("").length} minutes read</p>
            <h3>Preview</h3>
            <p>{text.length>0?text:"Enter Something in the textbox above to preview it"}</p>
        </div>
       
        </>
    )
}
